.tlbx-body {
  background: #f0f0f0; }

.tlbx-h2 {
  text-align: center; }

.styleguide {
  display: grid;
  grid-template-columns: 60px 240px 1fr;
  min-height: 100vh;
  max-width: 100%; }

.tlbx-toolbar-wrapper {
  grid-column: 1;
  background-color: #003baf;
  color: white; }

.tlbx-sidebar-wrapper {
  grid-column: 2;
  background-color: #f2f2f4; }

.tlbx-content-wrapper {
  grid-column: 3;
  -ms-flex-item-align: start;
      align-self: start;
  padding: 0 1rem;
  overflow: hidden; }



